body{
  background-color: #f4f6f9;
}

.container{
  max-width: 1340px;
}
div#main{
  margin: 65px 0 40px 0;
}
header{
  position: fixed;
  left:0;
  top:0;
  z-index:2000;
  width:100%;
  box-shadow: 0 6px 14px 0 rgba(0,0,0,.20);
  transition: box-shadow 0.4s linear;

  nav{
    padding: 15px!important;
  }
  a.nav-link{
    color: #2a2a2a !important;
    font-size: 17px;
    font-weight: 500;
  }
  a.nav-link:hover{
    color: #f44b40 !important
  }
}
.navbar-brand{
  padding: 0 15px;
}
.navbar-brand img{
  width: 65px;
}

.custom-toggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0,0,0)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.custom-toggler.navbar-toggler {
  border: none;
}

div.component-box{
  box-shadow: 0 0 0 1px rgba(13, 14, 16, 0.1);
  border-radius: 5px;
  background-color: #fff;
  margin-top: 15px;
}

section.blog-section{
  background-color: #fff;
  div.section-header{
    padding: 80px 0;
    text-align: center;
    font-family: 'Lato', sans-serif;

    .center-content{
      max-width: 700px;
      margin: 0 auto;
    }

    h1{
      font-size: 48px;
    }
    h3{
      font-size: 20px;
      font-weight: 100;
      color: #7e7e7e;
    }
  }
  .card{
    margin-bottom: 30px;
  }
  .card img{
    height: 200px;
    object-fit: cover;
  }
  .card-body{
    color: rgb(33, 37, 41);
  }
  div.card a:hover{
    text-decoration: none;
  }
  div.card:hover{
    box-shadow: 0 10px 6px 0 rgba(0,0,0,.05);
  }
}
section.blog-view-section{
  div.blog-item{
    margin-top: 15px;
    box-shadow: 0 0 0 1px rgba(13, 14, 16, 0.1);
    border-radius: 10px 10px 0 0;
  }
  div.blog-item img{
    width: 100%;
    height:340px;
    object-fit: cover;
    border-radius: 10px 10px 0 0;
  }
  div.blog-item-content{
    padding: 30px 60px;
    background-color: #fff!important;
    h1{
      font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
      font-size: 48px;
      font-weight: 800;
    }
    [class*='badge-']{
      padding: 8px 10px;
      border-radius: 10px;
    }
    div.text{
      color: #0a0b0c;
      font-size: 20px;
      line-height: 30px;
      padding: 30px 0;
    }
    div.author{
      padding: 10px 0;

      span.name{
        font-weight: 500;
        padding-right: 10px;
        padding-left: 3px;
        line-height: 42px;
      }
      span.date{
        font-size: 14px;
        color: #687481;
      }
    }
    div.author img{
      width: 36px;
      height: 36px;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  div.sticky-top{
    top: 80px;
  }
  div.sidebar-right-info{

    div.background-top{
      background-color: #7863ff;
      height: 35px;
      border-radius: 5px 5px 0 0;
    }
    div.author{
      font-weight: bold;
      font-size: 18px;
      padding: 20px 0 0 15px;
    }
    div.author img{
      width: 48px;
      height: 48px;
      border-radius: 50%;
      object-fit: cover;
      vertical-align: bottom;
    }
    div.text{
      padding: 20px;
      margin-top: 25px;
      color: #525C66;
    }
    div.text .btn{
      text-align: center;
      width: 100%;
    }

    div.property{
      font-size: 12px;
      color: #5F6B77;
      font-weight: bold;
    }
    div.value{
      font-size: 16px;
      color: #262A2F;
      line-height: 24px;
    }
  }

  div.sidebar-right-related{
    margin-top: 25px;
    color: #525C66;

    div.title{
      padding: 20px;
      color: #25292E;
      font-size: 20px;
      font-weight: bold;
      border-bottom: 1px solid #EAECED;
    }
    div.list{
      div.list-item{
        padding: 16px;
        border-bottom: 1px solid #EAECED;
        font-size: 16px;
        line-height: 24px;
      }
      div.list-item:hover{
        background-color: #f9f9f9;
      }
      div.list-title{
        padding-bottom: 4px;
      }
      div.list-title a{
        color: #262A2F;
      }
      div.list-title a:hover{
        color: #313DBB;
        text-decoration: none;
      }
      div.list-tags{
        color: #667380;
        font-size: 14px;
        span{
          color: #c4c4c4;
        }
        .tag-item{
          padding-right: 10px;
          display: inline-block;
        }
      }

    }
  }
}

.all-site {
  background-color: rgba(0, 0, 0, .8);
  height: 100%;
  left: 0;
  opacity: .9;
  top: 0;
  width: 100%;
  z-index: 999;
  position: fixed;
  transform: translateZ(0);
  display: none;
}

//MY ADMIN
section.my-admin{
  margin-left: 255px;

  div.content{
    margin-top: 130px;
    margin-bottom: 130px;
  }
  .breadcrumb{
    background-color: #fff!important;
    margin-bottom:0;
    box-shadow: 0 2px 2px 0 rgba(60,75,100,.14),0 3px 1px -2px rgba(60,75,100,.12),0 1px 5px 0 rgba(60,75,100,.2);
  }
