    color: #8a93a2!important;
    font-size: 14px!important;
    font-weight: 300;
  }
  .breadcrumb-item.active{
    color: #8a93a2!important;
  }

  section.admin-header-top {
    position: fixed;
    top: 0;
    left: 0;
    margin-left: 255px;
    width: 100%;
    z-index: 10;
    font-weight: 300;

    color: rgb(0,0,21,0.5);
    background-color: #fff;
    font-size: 14px;

