section.about-section{

  div.header-box{
    position:relative;
    margin-bottom: 140px;

    div.header-image{
      position: absolute;
      z-index: 10;
      top: 10px;
      left: 50%;
      transform: translateX(-50%);
    }
    div.header-image img{
      width: 115px;
      height: 115px;
      border-radius: 50%;
      border: 7px solid #7863ff;
    }
    div.header-bg{
      background-color: #7863ff;
      min-height: 140px;
      margin-top:56px;
    }
    div.header-body-position{

      position: absolute;
      top:65px;
      left: 20px;
      right: 20px;
    }
    div.header-body{
      background-color: #fff;
      padding:65px 20px 15px 20px;
      text-align: center;
      box-shadow: rgba(0, 0, 0, 0.1) 0 0 0 1px;
      border-radius: 6px;

      div.name{
        color: rgb(8, 9, 10);
        font-size: 30px;
        font-weight: 700;
      }
      div.date{
        line-height: 56px;
        color: #667380;
        i{
          padding-right: 5px;
          font-size: 20px;
        }
      }
    }
  }


  div.about-box{
    padding: 20px;
    margin-top: 10px;
    margin-bottom: 30px;

    div.job-position{
      background-color: rgb(114, 255, 154);
      display: inline-block;
      padding: 5px 20px;
      border-radius: 5px;
      font-weight: bold;
      font-size: 12px;
      color: rgb(28, 61, 37);
    }
    div.slogan-title{
      margin: 20px 0;
      padding-left: 10px;
      font-size: 24px;
      line-height: 36px;

      blockquote{
        font-size: 23px;
        font-family: Open Sans;
        font-style: italic;
        color: #555555;
        padding: 10px 30px 10px 55px;
        border-left: 6px solid #78C0A8;
        line-height: 1.6;
        position: relative;
        background: #EDEDED;
      }

      blockquote::before{
        font-family: Arial;
        content: "\201C";
        color: #78C0A8;
        font-size: 4em;
        position: absolute;
        left: 5px;
        top: -28px;
      }

      blockquote::after{
        content: '';
      }

      blockquote span{
        display:block;
        color:#333333;
        font-style: normal;
        font-weight: bold;
        margin-top:1em;
      }

    }
    div.achievement-box{
      border-radius: 8px;
      padding: 20px 40px 40px 40px;
      background-color: #2d3748;
      color: #fff;
      margin-bottom: 20px;

      div.title{
        color: #72ff9a;
        margin-bottom: 10px;
        font-size: 24px;
      }


    }

    div.author-image{
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 40px;

      div.author-image-child{
        position: relative;
        display: inline-block;
      }

      img{
        width: 500px;
        height: 500px;
        object-fit: cover;
        border-radius: 50%;
        opacity: 0.9;
      }
    }
    div[class*="lang"]{
      background-color: rgb(37, 38, 42, 0.9);
      box-shadow: rgba(0, 0, 0, 0.05) 0 0 0 10px;
      display: inline-block;
      border-radius: 50%;

      img{
        width: 80px;
        height: 80px;
        object-fit: cover;
        padding: 20px;
      }
    }


    @keyframes animation-lang1 {
      from {
        opacity:0.1;
        top: 100px;
        left: 30%
      }
      to {
        opacity:1;
        top:0;
        left: 0
      }
    }

    @keyframes animation-lang2 {
      from {
        opacity:0.1;
        top: 30%;
        right: 30%;
      }
      to {
        opacity:1;
        top:80px;
        right: -30px;
      }
    }

    @keyframes animation-lang3 {
      from {
        opacity:0.1;
        bottom: 30%;
        left:30%;
      }
      to {
        opacity:1;
        bottom:10px;
        left: 15px
      }
    }

    @keyframes animation-lang4 {
      from {
        opacity:0.1;
        bottom: 30%;
        right: 30%
      }
      to {
        opacity:1;
        bottom: 20px;
        right: 50px;
      }
    }

    div.lang1{
      position: absolute;
      top: 0;
      left:0;
      animation-name: animation-lang1;
      animation-duration: 1s;
      img{
        width: 160px;
        height: 160px;
      }
    }
    div.lang2{
      position: absolute;
      top: 80px;
      right: -30px;
      animation-name: animation-lang2;
      animation-duration: 1s;
      img{
        width: 120px;
        height: 120px;
      }
    }
    div.lang3{
      position: absolute;
      bottom:10px;
      left:15px;
      animation-name: animation-lang3;
      animation-duration: 2s;
      img{
        width: 120px;
        height: 120px;
      }
    }
    div.lang4{
      position: absolute;
      bottom:20px;
      right:50px;
      animation-name: animation-lang4;
      animation-duration: 3s;
    }

    #earth-orbit {
      left:0;
      //background-color: #7d7a7d;
      //z-index: 11111;
      //border-radius: 50%;
      width: 500px;
      height: 500px;
      position: absolute;
      top: 0;
      -webkit-animation: spin-right 20s linear infinite;
      -moz-animation: spin-right 20s linear infinite;
      -ms-animation: spin-right 20s linear infinite;
      -o-animation: spin-right 20s linear infinite;
      animation: spin-right 20s linear infinite;
    }

    @-webkit-keyframes spin-right {
      100% {
        -webkit-transform: rotate(360deg);
      }
    }

  }
}

//Contact section
section.contact-section{
  margin: 50px auto;
  max-width: 800px;
  font-family: Arial, sans-serif;

  h1.title {
    letter-spacing: 0;
    line-height: 1.2;
    color: #000;
    font-size: 36px;
    text-align: center;
    margin-bottom: 40px;
  }
  div.contact-form{
    background-color: #fff;
    box-shadow: 10px 10px 34px 0 rgba(0,0,0,.2);
    padding: 40px;
    margin-top: 50px;
    text-align: center;
  }
  input[type='text'],input[type='email'],textarea {
    border-radius: 4px;
    padding: 24px 20px;
    -webkit-appearance: none;
    font-weight: 500;
    outline: none;
    transition: border .2s ease-in-out;
  }
  input[type='submit'] {
    padding: 9px 100px;
    font-size: 18px;
  }

}


//Flash message
.new-flash-message{
  position: absolute;
  background-color: #fff;
  z-index: 10000;
  top: 75px;
  left: 50%;
  -webkit-transform: translateX(50%);
  -ms-transform: translateX(50%);
  transform: translateX(-50%);
  -webkit-box-shadow: 0px 0px 6px 6px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 6px 6px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  max-width:600px;
  width:90%;
  animation: flash-hidden 0.1s 3s linear forwards;

}

.new-flash-message.success{
  border-left: 8px solid green;
}
.new-flash-message.error{
  border-left: 8px solid #FD3030;
}
.new-flash-message .float-left{
  padding: 15px 20px;
  border-right: 1px solid rgba(208, 209, 231, 0.55);
  width: calc(100% - 100px);
}
.new-flash-message .float-left .title{
  font-weight: bold;
  font-family: "Raleway", SansSerif;
  font-size: 16px;
}
.new-flash-message .float-left .text{
  color: #b8b9cc;
}
.new-flash-message .flash-message-close{
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  right:0;
  color: #b8b9cc;
  font-size: 14px;
  width: 100px;
  text-align: center;
  font-weight: bold;
}
.new-flash-message .flash-message-close:hover{
  color: #62636d;
  cursor: pointer;
}

@keyframes flash-hidden {
  from {
    visibility: visible;
  }
  to {
    visibility: hidden;
    color: transparent;
    background-color: transparent;
    border-color: transparent;
  }
}



@media screen and (max-width: 767px) {

  .footer_block{
    padding: 0;
  }
  img.footer_logo {
    width: 100px;
  }
  .footer{
    padding: 20px 10px;
  }
  .footer_alt{
    padding: 20px;
  }
  section.blog-view-section div.blog-item-content {
    padding: 30px 20px;
  }


  section.about-section div.about-box div.author-image img.author-img{
    width:300px;
    height:300px;
  }
  section.about-section div.about-box #earth-orbit{
    width:300px;
    height:300px;
  }


  section.about-section div.about-box div[class*='lang'] img {
    width: 80px;
    height: 80px;
  }

  @keyframes animation-lang2 {
    from {
      opacity:0.1;
      top: 30%;
      right: 30%
    }
    to {
      opacity:1;
      top: 0;
      right: 0;
    }
  }

  section.about-section div.about-box div.lang2 {
    top: 0;
    right: 0;
  }
  @keyframes animation-lang4 {
    from {
      opacity:0.1;
      bottom: 30%;
      right: 30%
    }
    to {
      opacity:1;
      bottom: 10px;
      right: 15px;
    }
  }

  section.about-section div.about-box div.lang4 {
    position: absolute;
    bottom: 10px;
    right: 15px;
  }

}
